﻿﻿<script lang="ts">import { Post } from '@goldhill/Goldhill.ts';
import PostPresenterImage from './PostPresenterImage.svelte';
import PostPresenterBody from './PostPresenterBody.svelte';
import config from '@goldhill/config.ts';
export let post;
export let op;
export let thread;
export let summaryInfo;
export let onExpand;
export let inline = false;
export let summarized = false;
export let collapsed = false;
export let toggleCollapsed = () => (collapsed = !collapsed);
let summary = '';
if (summaryInfo) {
    if (summaryInfo.postsOmitted > 0 && summaryInfo.imagesOmitted > 0) {
        summary = `${summaryInfo.postsOmitted} posts and ${summaryInfo.imagesOmitted} image replies omitted.`;
    }
    else if (summaryInfo.postsOmitted > 0) {
        summary = `${summaryInfo.postsOmitted} posts omitted.`;
    }
    else if (summaryInfo.imagesOmitted > 0) {
        summary = `${summaryInfo.postsOmitted} images omitted.`;
    }
    else if (!summarized) {
        if (summaryInfo.postTotal > 0 && summaryInfo.imageTotal > 0) {
            summary = `${summaryInfo.postTotal} posts and ${summaryInfo.imageTotal} image replies shown.`;
        }
        else if (summaryInfo.postTotal > 0) {
            summary = `${summaryInfo.postTotal} posts shown.`;
        }
        else if (summaryInfo.imageTotal > 0) {
            summary = `${summaryInfo.imageTotal} image replies shown.`;
        }
    }
}
function toggleThread() {
    alert('toggle thread!');
}
//# sourceMappingURL=PostPresenter.svelte.js.map</script>

<style>
post-presenter {
    position: relative;
}

post-content {
    position: relative;
    background-color: #262b2f;
}

hide-btn {
    background-color: #33383e;
    border: 0px;
    padding: 3px;
    transition: background-color 60ms;
}

hide-btn:hover {
    background-color: #41464c;
}

show-replies-btn {
    /*background-color: #191d21;*/
    background-color: #262b2f;
    transition: background-color 60ms;
}

show-replies-btn:hover {
    background-color: #41464c;
}

:global(post-presenter .name-admin) {
    color: #dc5656;
    font-weight: 600;
}

:global(post-presenter .rainbow) {
    -webkit-animation-name: color-text-flow-keys;
    animation-name: color-text-flow-keys;
    -webkit-animation-duration: 6s;
    animation-duration: 6s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
    animation-direction: alternate;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
}
@keyframes color-text-flow-keys {
    0% {
        color: #d65c97;
    }
    5% {
        color: #5cd666;
    }
    10% {
        color: #a55cd6;
    }
    15.0% {
        color: #5c7cd6;
    }
    20% {
        color: #d65c7a;
    }
    25% {
        color: #81d65c;
    }
    30.0% {
        color: #835cd6;
    }
    35% {
        color: #685cd6;
    }
    40% {
        color: #5c9dd6;
    }
    45% {
        color: #5cd670;
    }
    50% {
        color: #d6625c;
    }
    55.0% {
        color: #d6835c;
    }
    60.0% {
        color: #d6605c;
    }
    65% {
        color: #be5cd6;
    }
    70% {
        color: #5c8dd6;
    }
    75% {
        color: #95d65c;
    }
    80% {
        color: #d69d5c;
    }
    85.0% {
        color: #d65c81;
    }
    90% {
        color: #5cd666;
    }
    95% {
        color: #d67e5c;
    }
    100% {
        color: #64d65c;
    }
}
</style>

<post-presenter
    class="w-100 flex flex-col {op && !collapsed ? '' : ''}
    {op ? `my-4 mb-2` : 'my-2 ml-6 mr-48'}"
    id={`pc${post.postNumber}`}
    op={op || undefined}
    reply={!op || undefined}>

    <post-content
        class="post block rounded-md px-3 {collapsed ? 'py-2' : op ? 'py-3' : 'py-2'}"
        style="margin-left: 0rem; padding-left: 2rem"
        id={inline ? `` : `p${post.postNumber}`}>
 
        {#if !collapsed}
            {#if op}
                <PostPresenterImage {post} />
                <PostPresenterBody {post} />
            {:else}
                <PostPresenterBody {post} />
                <PostPresenterImage {post} />
            {/if}
            <blockquote
                class="post-message {post.image != null && post.parentThread != null ? 'mt-4' : 'mt-1'}"
                id="m{post.postNumber}"
                contenteditable="false"
                bind:innerHTML={post.comment}>
            </blockquote>
            <div class="admin-notice">
                <!--<strong class="rainbow">[ USER WAS BANNED FOR THIS POST ]</strong>-->
            </div>
        {:else}
            {#if post.admin}
                <span class="name-admin">[ADMIN]</span>
            {/if}
            <span class="center">{post.name || config.anonName}</span>
        {/if}
    </post-content>
    {#if op && !collapsed && summary != ''}
        <show-replies-btn
            on:mousedown={onExpand}
            class="summary select-none block p-2 text-sm"
            style="margin-left: 1.2rem; color: #d8d6d9"
            role="button">
            <span class="text-blue-500 text-lg w-4 inline-block text-center">
                {summarized ? '+' : '-'}
            </span>
            {summary}
        </show-replies-btn>
    {/if}

    <hide-btn
        on:mousedown={(e) => toggleCollapsed()}
        class="block mr-2 select-none absolute float-left bg-black rounded-sm
        w-5 h-full cursor-pointer flex items-center justify-center"
        style="left: 0;">
        <span
            class="leading-none text-xl"
            style="margin-bottom: 0.1rem; color: #d6d6d6;">
            {collapsed ? '+' : '-'}
        </span>
    </hide-btn>
</post-presenter>
