﻿﻿<script lang="ts">import { Post } from '@goldhill/Goldhill.ts';
export let post;
let showFullImage = false;
let fileSizePretty = 0;
function forceDownload(url, fileName) {
    var xhr = new XMLHttpRequest();
    xhr.open('GET', url, true);
    xhr.responseType = 'blob';
    xhr.onload = function () {
        var urlCreator = window.URL || window['webkitURL'];
        var imageUrl = urlCreator.createObjectURL(this.response);
        var tag = document.createElement('a');
        tag.href = imageUrl;
        tag.download = fileName;
        document.body.appendChild(tag);
        tag.click();
        document.body.removeChild(tag);
    };
    xhr.send();
}
function onClickThumbnail(e) {
    e.preventDefault();
    return false;
}
function onClickDownload(e) {
    forceDownload(post.image.url, post.image.fileName);
    e.preventDefault();
    return false;
}
function vec2i(str) {
    let split = str.replace('{', '').replace('}', '').split(',');
    return { x: split[0], y: split[1] };
}
//# sourceMappingURL=PostPresenterImage.svelte.js.map</script>

<style>
.op img {
}

.google-search,
.yandex-search {
}
 
.thumb-image {
    border-radius: 0.4rem;
}

.download-button {
    width: 0.8rem;
    height: 0.8rem;
    margin-bottom: -2px;
    background-color: #649ec7;
    -webkit-mask: url('/i/download.svg') no-repeat center;
    mask: url('/i/download.svg') no-repeat center;
}
</style>

<div class="file break-words {post.parentThread == null ? 'op' : ''}">
    {#if post.image}
        <div
            class="fileText text-sm {post.parentThread == null ? '' : 'mt-2'}"
            style="color: #d8d6d9; line-height: 0;">
            <span class="fileInfo">
                File:{' '}
                <a href={post.image.url} class="fileName" target="_blank">
                    {post.image.fileName}
                </a>
                {' '}
                <div
                    class="fa cursor-pointer download-button inline-block"
                    on:mousedown={onClickDownload} />
                ({fileSizePretty}, {vec2i(post.image.dimensions).x}x{vec2i(post.image.dimensions).y})
            </span>
            {' '}
            <a
                target="_blank"
                class="google-search"
                href={`https://www.google.com/searchbyimage?image_url=${encodeURI(post.image.url)}`}>
                google
            </a>
            {' '}
            <a
                target="_blank"
                class="yandex-search"
                href={`https://www.yandex.com/images/search?rpt=imageview&img_url=${encodeURI(post.image.url)}`}>
                yandex
            </a>
        </div>
        <a class="fileThumb float-left contained mt-1" target="_blank">
            <img
                class="mt-2 mr-3 thumb-image {post.parentThread == null ? 'max-h-48' : 'max-h-32'}"
                href="#"
                on:mousedown={onClickThumbnail}
                src={post.image.url}
                alt={fileSizePretty} />
            {#if showFullImage}
                <img
                    class="full-image"
                    on:mousedown={onClickThumbnail}
                    src={post.image.url}
                    alt={fileSizePretty} />
            {/if}
        </a>
    {/if}
</div>
